/* reset */
* {margin: 0; padding: 0; box-sizing: border-box;}
ul {list-style: none;}
a {color: inherit; text-decoration: none;}
img {max-width: 100%;}
/*메인폰트*/
@font-face {
	font-family: 'SUITE';
	font-weight: 400;
	src: url('./font/SUITE-Regular.woff2') format('woff2');
}
@font-face {
	font-family: 'SUITE';
	font-weight: 500;
	src: url('./font/SUITE-Medium.woff2') format('woff2');
}
@font-face {
	font-family: 'SUITE';
	font-weight: 700;
	src: url('./font/SUITE-Bold.woff2') format('woff2');
}
body {
    font-family: 'SUITE',' sans-serif;';
    color: #fff;
    font-size: 16px;
	overflow-y: hidden;
}
/* color */
:root {
	--header-bg: #181818;
	--body-bg: #1f1f1f;
	--line: #2b2b2b;
	--select-point: #0078D4;
}

/* top_header */
.top_header {
	width: 100%;
	height: 42px;
	background-color: var(--header-bg);
	border-bottom: 1px solid var(--line);
	display: flex;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
}
.top_header nav ul {
	display: flex;
	padding-left: 11px;
}
.top_header nav ul li {
	margin-right: 20px;
	font-size: 14px;
	cursor: pointer;
}
.top_header nav ul li:first-child div {
	width: 19px;
	height: 19px;
	background-color: antiquewhite;
}
.top_header nav ul li:first-child {
	margin-right: 22px;
}
.top_header > div {
	width: 722px;
	height: 28px;
	background-color: #242424;
	border: 1px solid var(--line);
	border-radius: 7px;
	margin-left: 242px;
	cursor: pointer;
}
.top_header h1 {
	font-weight: 400;
	font-size: 12px;
	line-height: 28px;
	text-align: center;
}

/* left_iconarea */
.left_iconarea {
	width: 58px;
	height: calc(100vh - 42px);
	background-color: var(--header-bg);
	border-right: 1px solid var(--line);
	margin-top: 42px;
	padding-top: 13px;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 30px;
	overflow: hidden;
	position: fixed;
	left: 0;
	top: 0;
}
.left_iconarea div {cursor: pointer}

/* left_opentab */
.left_opentap {
	width: 307px;
	height: calc(100vh - 42px);
	background-color: var(--header-bg);
	border-right: 1px solid var(--line);
	position: fixed;
	top: 42px;
	left: 58px;
}
.left_opentap .tool {
	width: 100%;
	height: 65px;
	display: flex;
	padding: 0 30px;
	justify-content: space-between;
	align-items: center;
}
.left_opentap .tool h3 {
	font-size: 16px;
	font-weight: 400;
}
.left_opentap .tool div span {
	display: inline-block;
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #fff;
}
.left_opentap .tool div span:nth-child(2) {margin: 0 3px;}
.left_opentap .file_name {
	width: 100%;
	height: 28px;
	display: flex;
}
.left_opentap .file_name div {margin: 0 10px 0 10px;}
.left_opentap .file_name h2 {font-size: 16px;}
.left_opentap .file_list {
	display: flex;
	width: 100%;
	height: 37px;
	align-items: center;
	cursor:pointer;
	font-size: 14px;
}
.left_opentap .file_list div {margin: 0 10px 0 30px;}

/* top_opentab */
.top_opentab {
	background-color: var(--header-bg);
	border-bottom: 1px solid var(--line);
	width: calc(100% - 365px);
	height: 42px;
	position: fixed;
	top: 42px;
	left: 365px;
}
.top_opentab nav {height: 100%;}
.top_opentab nav ul {
	display: flex; 
	height: 100%;
}
.top_opentab nav ul li {
	height: 100%;
	display: flex;
	padding-left: 18px;
	border-right: 1px solid var(--line);
	line-height: 42px;
	font-size: 14px;
}
.top_opentab nav ul li > div:nth-child(1) {margin-right: 10px;}
.top_opentab nav ul li > p {cursor: pointer;}
.top_opentab nav ul li > div:nth-child(3) {margin: 0 20px 0 30px; cursor: pointer;}

/* 웹프로젝트 */
.web, .uiux, .profile, .design, .etc, .error {
	width: calc(100% - 365px);
	height: 2000px;
	background-color: var(--body-bg);
	margin: 84px 0 0 365px;
	overflow-y: auto;
}